* {
  margin: 0;
  padding: 0;
}

#root {
    box-sizing: border-box;
    height: 93vh;
}

a {
    text-decoration: none;
    color: inherit;
}

a:hover {
    outline: none;
    border-radius: 0;
}

.loading {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.header .row {
    display: flex;
    justify-content: space-between;
}
.mainmenu {
    min-width: 33vw;
}

.goalform {
    width: 80vw;
    min-width: 320px;
    max-width: 960px;
    /* min-height: 600px; */
}

.goalform .row {
    display: flex;
}

.goalform .row.right {
    justify-content: flex-end;
}
