.goalitem-strike {
    -webkit-animation: strike 750ms ease 1 forwards;
            animation: strike 750ms ease 1 forwards;
}

@-webkit-keyframes strike{
    0%   { opacity : 1 }
    100% { 
            -webkit-text-decoration: line-through #ffffff; 
                    text-decoration: line-through #ffffff;
            opacity: 0.75
         }
}

@keyframes strike{
    0%   { opacity : 1 }
    100% { 
            -webkit-text-decoration: line-through #ffffff; 
                    text-decoration: line-through #ffffff;
            opacity: 0.75
         }
}
.centeredModal {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
    align-items: center; 
}
.dash {
    position: relative;
    width: 100%;
    height: 100%;
    max-width: 960px;
    margin: auto;
}

.dash-clock {
    display: flex;
    /* justify-content: center; */
    align-items: center;
    margin: 11px 32px 11px 16px;
    /* width: 100%; */
}

#dash-addBtn {
    position: fixed;
    bottom: 40px;
    right: 40px;
    padding: 0;
}

#dash-addBtn:hover {
    padding: 0 16px;
}

#dash-addBtn:hover::after {
    content:"Add Goal";
    white-space:nowrap;
}
.App {
    height: 100%;
    width: 100%;
}
